import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Router } from "@angular/router";
import { environment } from "../../environments/environment";


@Injectable()
export class AuthService {
    constructor(private httpClient: HttpClient,
        private router: Router) { }
    currentUser;
    path = environment.gatewayPath + "auth/signin";


    login(loginBody: any) {
        return this.httpClient.post(this.path, loginBody);
    }

    setUser(user) {
        this.currentUser = user;
        localStorage.setItem("@ng-test", JSON.stringify(this.currentUser));
    }
    authUser() {
        let user = localStorage.getItem("@ng-test");
        if (user) {
            this.currentUser = JSON.parse(user);
        }
    }

    isAuthenticated(): boolean {
        return this.currentUser != null;
    }

    getToken() {
        return this.currentUser.accessToken;
    }
    logout() {
        this.currentUser = null;
        localStorage.removeItem('@ng-test');
        this.router.navigate(['signin']);
    }
}