import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Observable } from "rxjs";
import { Page } from "./models/page.model";
import { environment } from "../environments/environment";
import { Chat } from "./models/chat.model";
@Injectable()
export class OrderServices {
    constructor(private httpClient: HttpClient) { }
    path = environment.path + "providers/" + environment.id + "/orders";
    basePath = environment.path;
    token = { headers: new HttpHeaders().set('Authorization', 'Bearer  eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJzdWIiOjMxNSwiaXNzIjoiQXBwIiwiaWF0IjoxNTM2NDkyMTA5NzgxLCJleHAiOjE1MzY0OTIxMTY5ODF9.wYPCAEDMcUC2JvmBXxT5z2w8Jf2KL7ilVgEsYLQPuOk') };

    denyPausation(providerId, orderId) {
        let url = this.basePath + 'providers/' + providerId + '/orders/' + orderId + '/deny-pausation';
        return this.httpClient.put(url, {});
    }

    denyResumption(providerId, orderId) {
        let url = this.basePath + 'providers/' + providerId + '/orders/' + orderId + '/deny-resumption';
        return this.httpClient.put(url, {});
    }

    updateproviderorders(orderId: number, status: string) {
        let url = this.basePath + "orders/" + orderId + "/status";
        return this.httpClient.patch(url, { "status": status });
    }

    resetUnseenCount(providerId) {
        let url = this.basePath + 'users/' + providerId + '/notifications/count-reset';
        return this.httpClient.put(url, {});
    }
    sendMessage(messageObject, conversationId) {
        let url = this.basePath + "chat/" + conversationId;
        return this.httpClient.post(url, messageObject);
    }
    getConversationMessages(conversationId): Observable<any[]> {
        let url = this.basePath + "chat/" + conversationId;
        return this.httpClient.get<any[]>(url);
    }
    getOrderById(providerId, orderId: number) {
        let url = this.basePath + "providers/" + providerId + "/orders/" + orderId;
        return this.httpClient.get(url);
    }

    fetchproviderorders(providerId, page: number): Observable<Page<any>> {
        let url = this.basePath + "providers/" + providerId + "/orders" + "?page=" + page + "&limit=" + 10;
        return this.httpClient.get<Page<any>>(url);
    }
    getConversationId(person1: number, person2: number) {
        let url = this.basePath + "conversationId?id1=" + person1 + "&id2=" + person2;
        return this.httpClient.get(url);
    }

    updateprovideLocation(provider: number, body: any) {
        let url = this.basePath + 'providers/' + provider + '/current-location';
        return this.httpClient.put(url, body);
    }

    fetchproviderNotifications(providerId, page: number): Observable<Page<any>> {
        let url = this.basePath + "users/" + providerId + "/notifications?page=" + page + "&limit=" + 12;
        return this.httpClient.get<Page<any>>(url);
    }

    getProviderLocation(providerId) {

        let url = this.basePath + 'providers/' + providerId + '/current-location';
        return this.httpClient.get(url);
    }

    getClientById(ClientId) {
        let url = this.basePath + 'clients/' + ClientId;
        return this.httpClient.get(url);
    }

    getReasons(type) {
        let url = this.basePath + 'reasons?type=' + type;
        return this.httpClient.get(url);
    }
    addReasons(orderId, reasonBody) {
        let url = `${this.basePath}orders/${orderId}/reason`;
        return this.httpClient.patch(url, reasonBody);
    }

    laterPayment(orderId) {
        let url = `${this.basePath}orders/${orderId}/payment/not-waiting`;
        return this.httpClient.patch(url, {});
    }

    providerConfirmCash(providerId: number, orderId: number,body) {
        let url = `${this.basePath}providers/${providerId}/orders/${orderId}/confirm-cash`;
        return this.httpClient.put(url, body);
    }

    getUnseenNotification(userId: number) {
        let url = `${this.basePath}users/${userId}/notifications/unseen-count`;
        return this.httpClient.get(url);
    }


}