import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { AuthService } from '../service/auth.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-login-component',
  templateUrl: './login-component.component.html',
  styleUrls: ['./login-component.component.css']
})
export class LoginComponent implements OnInit {
  singInForm: FormGroup;
  constructor(private authService:AuthService,
    private router: Router,
		private activeRoute: ActivatedRoute) { }

  ngOnInit() {
    this.singInForm = new FormGroup({
      'userName': new FormControl(null, [Validators.required]),
      'password': new FormControl(null, [Validators.required]),
    });
  }

  onLoginSubmit() {
    let userName = this.singInForm.get('userName').value;
    let password = this.singInForm.get('password').value;
    if (!this.singInForm.valid) {
      Object.keys(this.singInForm.controls).forEach(key => {
        this.singInForm.get(key).markAsTouched();
      })
      return;
    }
   
    let loginBody={
      "username":userName,
      "password":password,
    }
    console.log('loginBody====> ',loginBody);
    this.authService.login(loginBody).subscribe(
      (result)=>{

        this.authService.setUser(result);
        let returnUrl = this.activeRoute.snapshot.queryParams["url"];
        if (returnUrl)
          this.router.navigate([returnUrl]);
        else
          this.router.navigate(["/"]);
      }
    )
  }

}
