import { Component, OnInit } from '@angular/core';
import ds from '../da.service';
import { AuthService } from '../service/auth.service';
import { OrderServices } from '../order.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  providerId;
  notificationCount;
  constructor(
    private authService: AuthService,
    private orderServices: OrderServices,
    private router: Router
  ) { }

  ngOnInit() {
    let user = this.authService.currentUser.user;
    this.providerId = user.id;

    this.getUnseenCountNotification();
    this.listenToNotification();
  }

  listenToNotification() {
    ds.record.getRecord(`dsNotifications/${this.providerId}`).subscribe(
      (res) => {
        if (res.unseenCount > 0) {
          console.log('the notifcation ======', res);
          this.notificationCount = res.unseenCount;
        }
      });
  }

  getUnseenCountNotification() {
    this.orderServices.getUnseenNotification(this.providerId)
      .subscribe(res => {
        this.notificationCount = res['unseenCount'];
        console.log('the Notification =======>>>', res);
      })

  }

  logOut() {
    this.authService.logout();
  }

  goToNtifcation() {
    this.router.navigate(['provider', this.providerId, 'notification']);
    if (this.notificationCount > 0) this.resetUnseenCount();
  }

  resetUnseenCount() {
    this.orderServices.resetUnseenCount(this.providerId)
      .subscribe(result => {
        this.notificationCount = 0;
        console.log(result, 'resetUnseenCount')
      }
      )
  }
}
