import { Component, OnInit } from '@angular/core';
import { OrderServices } from '../order.service';
import ds from '../da.service';
import { environment } from '../../environments/environment';
import { AuthService } from '../service/auth.service';

@Component({
  selector: 'app-showorder-notification',
  templateUrl: './showorder-notification.component.html',
  styleUrls: ['./showorder-notification.component.css']
})
export class ShoworderNotificationComponent implements OnInit {

  constructor(private orderServices: OrderServices,
    private authService: AuthService) { }
  notification = [];
  loading;
  providerId;
  noMoreData;
  page = 1;
  totalPages = 1;
  ngOnInit() {
    let user = this.authService.currentUser.user;
    this.providerId = user.id;
    this.fetchnotification(1);
  }


  onLoadMoreClick() {
    this.page++;
    this.fetchnotification(this.page);
  }

  fetchnotification(page) {
    this.loading = true;
    this.orderServices.fetchproviderNotifications(this.providerId,page)
      .subscribe(result => {
        this.notification = this.notification.concat(result.data);
        console.log('the result', result);
        this.totalPages = result.pageCount;

        this.loading = false;

        if (this.page == this.totalPages)
          this.noMoreData = true;

      }
      )
  }

}
